import cuPanel from '@/components/custom/cu-panel.vue'
import cuCell from '@/components/custom/cu-cell.vue'
import cuCellGroup from '@/components/custom/cu-cell-group.vue'
import uniList from '@/components/uni-list/uni-list.vue'
import uniListItem from '@/components/uni-list-item/uni-list-item.vue'
import { api } from '@/config/common.js'
import { query, create } from '@/api/common.js'
import getGlobalData from '@/utils/business.js'
export default {
	components: {
		cuPanel,
		cuCell,
		cuCellGroup,
		uniList,
		uniListItem
	},
	data() {
		return {
			title: '采购付款',
			reqData: {
				order: {
					billtype: 1,
					isOnCredit: 0,
					payaccountid: '',
					payaccountName: '',
					contactunitid: '',
					contactunitname: '',
					telephone: '',
					amount: 0.00,
					isprint: 0,
					discountamount: ''
				},
				orderlist: []
			},
			tmpAmount: 0.00,
			cashAccountDict: [],
			discount: 0,
			showDisCount: '',
			verify: {
				payaccountName: { okVerify: false, disVerMessage: false, message: '付款帐号不能为空' }
			}
		};
	},
	onLoad(options) {
		if (options) {
			let data = JSON.parse(options.reqData)
			this.reqData.order.contactunitid = data.contactunitid
			this.reqData.order.contactunitname = data.contactunitname
			this.reqData.order.telephone = data.telephone
			this.reqData.order.amount = parseFloat(data.totalPrice).toFixed(2)
			this.tmpAmount = this.reqData.order.amount
			this.reqData.orderlist = data.productList
		}
		this.getCashAccount()
	},
	computed: {
		headerHeight() {
			return this.$headerHeight
		},
		mainHeight() {
			return this.$mainHeight
		}
	},
	methods: {
		handleNavbarClickLeft() {
			let pages =  getCurrentPages()
			let prevPage = pages[pages.length - 2]
			prevPage.setData({
				commandType: 'return'
			})
			uni.navigateBack({
				delta: 1
			})
		},
		getCashAccount() {
			this.$refs.loading.open()
			query(api.cashAccount).then(res => {
				this.$refs.loading.close()
				if (res.status == 200 && res.data.returnCode == '0000') {
					this.cashAccountDict = res.data.data.resultList
				} else {
					uni.showToast({
						icon: 'none',
						title: res.data.returnMessage
					})
				}
			}).catch(error => {
				this.$refs.loading.close()
				uni.showToast({
					icon: 'none',
					title: error
				})
			})
		},
		handleCreditChange(val) {
			this.reqData.order.isOnCredit = val.detail.value
		},
		handleSelectCashAccount(val) {
			this.reqData.order.payaccountid = val.cashaccountid
			this.reqData.order.payaccountName = val.cashaccountname
			this.handleVerify('payaccountName')
		},
		handlePrintChange(val) {
			this.reqData.order.isprint = val.detail.value
		},
		handleDisCount(val) {
			if (val.detail.value) {
				this.reqData.order.discountamount = '0.' + this.tmpAmount.split('.')[1]
				this.reqData.order.amount = this.tmpAmount.split('.')[0] + '.00'
			} else {
				this.reqData.order.discountamount = '0.00'
				this.reqData.order.amount = this.tmpAmount
			}
		},
		handleVerify(val) {
			switch(val) {
				case 'payaccountName':
					if (!this.reqData.order.payaccountName) {
						this.verify.payaccountName.okVerify = false
						this.verify.payaccountName.disVerMessage = true
					} else {
						this.verify.payaccountName.okVerify = true
						this.verify.payaccountName.disVerMessage = false
					}
					break
			}
		},
		checkVerify() {
			let result = true
			for (let item in this.verify) {
				if (!this.verify[item].okVerify) {
					this.verify[item].disVerMessage = true
					result = false
				}
			}
			return result
		},
		handleSubmit() {
			if (this.checkVerify()) {
				this.$refs.loading.open()
				create(api.purPurchaseOrder, this.reqData).then(res => {
					this.$refs.loading.close()
					if (res.status == 200 && res.data.returnCode == '0000') {
						getGlobalData.getCurrentUnit().then(res => {
							uni.showToast({
								icon: 'success',
								title: '提交成功'
							})
							setTimeout(()=>{
								let pages =  getCurrentPages()
								let prevPage = pages[pages.length - 2]
								prevPage.setData({
									commandType: 'success'
								})
								uni.navigateBack({
									delta: 1
								})
							},500)
						}).catch(err => {
							uni.showToast({
								icon: 'success',
								title: '提交成功'
							})
							setTimeout(()=>{
								let pages =  getCurrentPages()
								let prevPage = pages[pages.length - 2]
								prevPage.setData({
									commandType: 'success'
								})
								uni.navigateBack({
									delta: 1
								})
							},500)
						})
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.returnMessage
						})
					}
				}).catch(error => {
					this.$refs.loading.close()
					uni.showToast({
						icon: 'none',
						title: error
					})
				})
			}
		}
	},
	watch: {
		'reqData.order.discountamount': {
			handler(val) {
				if (val && val !== '0.00') {
					this.showDisCount = '抹零金额：￥' + this.reqData.order.discountamount
				} else {
					this.showDisCount = ''
				}
			},
			deep: true
		}
	}
}
